<template>
  <div class="modal fade" id="AdmincartModal"  tabindex="-1" aria-hidden="true">
    <div class="modal-dialog modal-lg">
      <div class="modal-content border-0">

        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title"></h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span class="text-light" aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="modal-body">
          <div class="d-flex justify-content-center" v-if="cart_menu.carts.length !== 0">
            <div class="col-12">
              
              <div class="my-4 buyList">
                <table class="table overFlowTable">
                  <thead>
                    <th width="10"></th>
                    <th class="text-danger" width="200"></th>
                    <th class="text-danger" width="120"></th>
                    <th class="text-danger" width="40"></th>
                  </thead>
                  <tbody>
                    <tr v-for="(item) in cart_menu.carts" :key="item.id">
                      <td>
                        <button type="button" class="btn btn-outline-danger btn-sm" @click="removeItem(item.id)">
                          <i class="far fa-trash-alt"></i>
                        </button>
                      </td>
                      <td>
                        {{item.product.title}}
                        <div class="text-success" v-if="item.coupon">
                          
                        </div>
                      </td>
                      <td>{{item.qty}}/{{item.product.unit}}</td>
                      <td>{{item.total | currency}}</td>
                    </tr>
                    <tr>
                      <td></td>
                      <td colspan="2" class="text-right"><h5></h5></td>
                      <td  v-if="cart_menu.final_total !== cart_menu.total" class="text-right">
                        <h5><del>{{cart_menu.total | currency}}</del></h5>
                      </td>
                      <td  v-else class="text-right">
                        <h5>{{cart_menu.total | currency}}</h5>
                      </td>
                    </tr>
                    <tr class="text-success" v-if="cart_menu.final_total !== cart_menu.total">
                      <td></td>
                      <td colspan="2" class="text-right">
                        <h5></h5>
                      </td>
                      <td class="text-right">
                        <h5>{{cart_menu.final_total | currency}}</h5>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <div class="input-group mb-3 input-group">
                <input type="text" class="form-control" placeholder="" v-model="couponCode">
                <div class="input-group-append">
                  <button class="btn btn-outline-secondary" type="button" @click="sendCouponCode">
                    
                  </button>
                </div>
              </div>

            </div>
          </div>

          <div class="row justify-content-center my-5 " v-if="cart_menu.carts.length !== 0">
            <form class="col-md-10">
              <div class="form-group">
                <label for="useremail">E-mail</label>
                <input type="email" id="useremail" name="email" class="form-control" placeholder="Email" :class="{'is-invalid':errors.has('email')}"
                 v-model="form.user.email" v-validate="'required|email'" required>
                <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
              </div>

              <div class="form-group">
                <label for="username"></label>
                <input type="text" id="username" name="name" class="form-control" placeholder="" :class="{'is-invalid':errors.has('name')}"
                v-model="form.user.name"  v-validate="'required'" required>
                <span class="text-danger" v-if="errors.has('name')"></span>
              </div>

              <div class="form-group">
                <label for="usertel"></label>
                <input type="text" id="usertel" name="tel" class="form-control" placeholder="" :class="{'is-invalid':errors.has('tel')}"
                v-model="form.user.tel" v-validate="'required|regex:^[0-9]+'" required>
                <span class="text-danger" v-if="errors.has('tel')">()</span>
              </div>

              <div class="form-group">
                <label for="useraddress"></label>
                <input type="text" id="useraddress" name="address" class="form-control" placeholder="" :class="{'is-invalid':errors.has('address')}"
                v-model="form.user.address" v-validate="'required'" required>
                <span class="text-danger" v-if="errors.has('address')"></span>
              </div>

              <div class="form-group">
                <label for="usermessage"></label>
                <textarea type="textarea" id="usermessage" name="message" class="form-control" v-model="form.message" ></textarea>
              </div>

            </form>
         </div>

          <div v-else>
            
          </div>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <button type="button" class="btn btn-dark" @click.prevent="submitOrder"></button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{
      form:{
        "user": {
          "name": "",
          "email": "",
          "tel": "",
          "address": ""
        },
        "message": ""
      },
      couponCode:''
    }
  },
  methods:{
    getCartData() {
      this.$store.dispatch("init_cart_data");
    },
    removeItem(id){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`

      this.$http.delete(api).then((response)=>{
        if(response.data.success){
          this.getCartData()
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    },
    sendCouponCode(){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`
      const code = {
        code: this.couponCode
      }

      this.$http.post(api,{data: code}).then((response)=>{
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
          this.this.getCartData()
          this.couponCode = ''
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    },
    submitOrder(){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`
      const order = this.form

      this.$validator.validate().then((valid) => {
        if(valid){
          this.$http.post(api,{data: order}).then((response)=>{
            if(response.data.success){
              this.$router.push(`/admin/order_check/${response.data.orderId}`)
            }
            else{
              this.$bus.$emit('message:push',response.data.message,'danger');
            }
            $('#AdmincartModal').modal('hide')
          })
        }
      })

    }
  },
  created(){
    this.getCartData()
  },
  computed:{
    cart_menu(){
      return this.$store.getters['CART_MENU']
    }
  }
}
</script>

<style lang="scss" scoped>
.buyList{
  max-width: 100%;
  overflow:auto;
}

@media only screen and (max-width:400px){
  .overFlowTable{
    width: 400px;
  }
}
</style>