<template>
  <div>
    <table class="table rwdTable">
      <thead>
        <th width="120"></th>
        <th width="60"></th>
        <th width="60"></th>
        <th width="60"></th>
        <th width="60"></th>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons_menu" :key="item.id">
          <td scope="col" class="rwdTitle" data-title="">{{item.title}}</td>
          <td scope="col" class="rwdTitle" data-title="">{{item.percent}} %</td>
          <td scope="col" class="rwdTitle" data-title="">{{item.due_date|timeChanger}}</td>
          <td scope="col" class="rwdTitle" data-title="">
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td scope="col" class="rwdTitle" data-title="">
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm" @click="editHandler(item)"></button>
              <button class="btn btn-outline-danger btn-sm" @click="delHandler(item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  data(){
    return{}
  },
  props:{
    coupons_menu:{
      type:Array,
      required:true
    }
  },
  methods:{
    editHandler(data){
      $('#addCouponModal').modal('show')
      this.$emit('changeHandler',data)
    },
    delHandler(data){
      $('#delCouponModal').modal('show')
      this.$emit('changeHandler',data)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/helper/_rwdTable";
</style>